[v-cloak] > * { display:none; }
[v-cloak]::before {
  content: " ";
  display: block;
  width: 40px;
  height: 40px;
  position: absolute;
  top: 50%;
  left: 50%;
  background-image: url('data:image/svg+xml;base64,');
}

.pace .pace-progress {
  background: #0D47A1;
}

.pace .pace-progress-inner {
  box-shadow: 0 0 10px #0D47A1, 0 0 5px #0D47A1;
}

.pace .pace-activity {
  border-top-color: #0D47A1;
  border-left-color: #0D47A1;
}


